import React, { useState } from "react";
import "./Contact.css";
import InputAdornment from "@material-ui/core/InputAdornment";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import SubjectIcon from "@material-ui/icons/Subject";
import PersonIcon from "@material-ui/icons/Person";
import MessageIcon from "@material-ui/icons/Message";
import EmailIcon from "@material-ui/icons/Email";
import { Grid, Paper, TextField, Button } from "@material-ui/core";
import emailjs from "emailjs-com";

export default function Contact() {
  const SERVICE_ID = "service_n0xwghy";
  const USER_ID = `user_tsJytottsG8NHdgEF1vJd`;
  const TEMP_ID = "template_34zjjbq";
  const [sub, setsub] = useState("");
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [msg, setmsg] = useState("");
  const signin = (e) => {
    e.preventDefault();
    if (!name || !email || !msg || !sub) {
      alert("Credentials can not be blank !!");
    } else {
      e.preventDefault();
      let temp_params = {
        name: name,
        text: msg,
        email: email,
        subject: sub,
      };
      emailjs.send(SERVICE_ID, TEMP_ID, temp_params, USER_ID).then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
      console.log("done");
    }
    setname("");
    setemail("");
    setmsg("");
    setsub("");
  };
  return (
    <Grid className="grid">
      <Paper
        className="contact_paper"
        elevation={10}
        onSubmit={(e) => signin(e)}
      >
        <ContactMailIcon style={{ color: "#283557" }} />
        <h3>Contact Us</h3>
        <TextField
          style={{ margin: "10px auto", width: "80%" }}
          id="input-with-icon-textfield"
          label="Your Name"
          value={name}
          onChange={(e) => setname(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment
                style={{ paddingRight: "5px", paddingLeft: "5px" }}
                position="start"
              >
                <PersonIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          style={{ margin: "10px auto", width: "80%" }}
          id="input-with-icon-textfield"
          label="Your Email"
          value={email}
          onChange={(e) => setemail(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment
                style={{ paddingRight: "5px", paddingLeft: "5px" }}
                position="start"
              >
                <EmailIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          style={{ margin: "10px auto", width: "80%" }}
          id="input-with-icon-textfield"
          label="Your Subject"
          value={sub}
          onChange={(e) => setsub(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment
                style={{ paddingRight: "5px", paddingLeft: "5px" }}
                position="start"
              >
                <SubjectIcon />
              </InputAdornment>
            ),
          }}
        />
        <TextField
          style={{ margin: "10px auto", width: "80%" }}
          id="input-with-icon-textfield"
          label="Your Message"
          multiline
          maxRows={10}
          value={msg}
          onChange={(e) => setmsg(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment
                style={{ paddingRight: "5px", paddingLeft: "5px" }}
                position="start"
              >
                <MessageIcon />
              </InputAdornment>
            ),
          }}
        />
        <Button
          style={{ marginTop: "10px", width: "60%", height: "40px" }}
          color="primary"
          type="submit"
          onClick={(e) => signin(e)}
        >
          Send
        </Button>
      </Paper>
    </Grid>
  );
}
