import HomeIcon from "@material-ui/icons/Home";
import PhotoLibraryIcon from "@material-ui/icons/PhotoLibrary";
import img from "./images/CSL2/logo111.jpg";
import InfoIcon from "@material-ui/icons/Info";
import ChatIcon from "@material-ui/icons/Chat";
import MenuIcon from "@material-ui/icons/Menu";
import AppsIcon from "@material-ui/icons/Apps";
import "./Navbar.css";
import React from "react";
import { Link } from "react-router-dom";

const Example = () => {
  return (
    <div>
      <link
        href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.0/dist/css/bootstrap.min.css"
        rel="stylesheet"
        integrity="sha384-KyZXEAg3QhqLMpG8r+8fhAXLRk2vvoC2f3B09zVXn8CA5QIVfZOJ3BCsw2P0p/We"
        crossOrigin="anonymous"
      />

      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            <img className="logo" src={img} alt="CSH" />
            <div className="heading">
              <h2 className="first_name">CHARBHUJA</h2>
              <h2 className="second_name">
                <p>CARGO</p>
                <p>HANDLERS</p>{" "}
              </h2>
            </div>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
            style={{ border: "2px solid white" }}
          >
            <MenuIcon style={{ color: "#fff" }} />
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  <HomeIcon style={{ fontSize: 30 }} />
                  <h4>Home</h4>
                </Link>
              </li>
              <li className="nav-item dropdown">
                <Link
                  className="nav-link"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <AppsIcon style={{ fontSize: 30 }} />
                  <h4>Services</h4>
                </Link>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <Link className="dropdown-item" to="/air">
                      Air Frieght
                    </Link>
                  </li>
                  <li>
                    <Link className="dropdown-item" to="/sea">
                      Sea Frieght
                    </Link>
                  </li>
                  <li>
                    <Link className="dropdown-item" to="/road">
                      Road Frieght
                    </Link>
                  </li>
                  <li>
                    <Link className="dropdown-item" to="/cargo">
                      Project Cargo
                    </Link>
                  </li>
                </ul>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/gallery"
                >
                  <PhotoLibraryIcon style={{ fontSize: 30 }} />
                  <h4>Gallery</h4>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/contact"
                >
                  <ChatIcon style={{ fontSize: 30 }} />
                  <h4>Contact Us</h4>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  aria-current="page"
                  to="/about"
                >
                  <InfoIcon style={{ fontSize: 30 }} />
                  <h4>About Us</h4>
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Example;
