import React, { useState } from "react";
import { motion } from "framer-motion";
import img1 from "../images/CSL/4.jpg";
import img2 from "../images/CSL/5.jpg";
import img3 from "../images/CSL/6.jpg";
import img4 from "../images/CSL/7.jpg";
import img5 from "../images/CSL/8.jpg";
import img6 from "../images/CSL/9.jpg";
import img7 from "../images/CSL/54.jpg";
import img8 from "../images/CSL2/SAL-Reefer.jpg";
import img9 from "../images/CSL/containers.jpg";
import img10 from "../images/CSL/Cargo.jpg";
import img11 from "../images/CSL/50.jpg";
import img12 from "../images/CSL2/speed-1280x720-1.jpg";

import "./Gallery.css";

export default function Gallery() {
  const [selectedImg, setSelectedImg] = useState(null);
  return (
    <div className="gallery">
      <ImageGrid setSelectedImg={setSelectedImg} />
      {selectedImg && (
        <Modal selectedImg={selectedImg} setSelectedImg={setSelectedImg} />
      )}
    </div>
  );
}

const ImageGrid = ({ setSelectedImg }) => {
  const docs = [
    img1,
    img2,
    img3,
    img4,
    img5,
    img6,
    img7,
    img8,
    img9,
    img10,
    img11,
    img12,
  ];

  return (
    <div className="img-grid">
      {docs &&
        docs.map((doc) => (
          <motion.div
            className="img-wrap"
            key={doc.id}
            layout
            whileHover={{ opacity: 1 }}
            s
            onClick={() => setSelectedImg(doc)}
          >
            <motion.img
              src={doc}
              alt=""
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1 }}
            />
          </motion.div>
        ))}
    </div>
  );
};

const Modal = ({ setSelectedImg, selectedImg }) => {
  const handleClick = (e) => {
    if (e.target.classList.contains("backdrop")) {
      setSelectedImg(null);
    }
  };

  return (
    <motion.div
      className="backdrop"
      onClick={handleClick}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
    >
      <motion.img
        src={selectedImg}
        alt="enlarged pic"
        initial={{ y: "-100vh" }}
        animate={{ y: 0 }}
      />
    </motion.div>
  );
};
