import React from "react";
import "./Footer.css";
import img from "./images/CSL2/logo.jpg";
import { Link } from "react-router-dom";

export default function Footer() {
  return (
    <div className="footer">
      <div className="footer_logo">
        <img src={img} alt="logo" />
      </div>
      <div className="pages">
        <h3>Pages</h3>
        <span>
          <Link className="page_link" to="/">
            Home
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/gallery">
            Gallery
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/about">
            About Us
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/contact">
            Contact us
          </Link>
        </span>
      </div>
      <hr />
      <div className="services">
        <h3>Services</h3>
        <span>
          <Link className="page_link" to="/sea">
            Sea Freight
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/air">
            Air Freight
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/road">
            Road Freight
          </Link>
        </span>
        <span>
          <Link className="page_link" to="/cargo">
            Project Cargo
          </Link>
        </span>
      </div>
      <hr />
      <div className="contact_info">
        <h3>Reach</h3>
        <div className="company_details">
          <span>Charbhuja Cargo Handlers</span>
          <span>Office no., Area</span>
          <span> City, State, Country</span>
          <span>company@gmail.com</span>
        </div>
      </div>
    </div>
  );
}
