import img1 from "../images/CSL2/maersk-reefers.jpg";
import img2 from "../images/CSL2/sea1.jpg";
import img3 from "../images/CSL2/Seatrade Blue alongside Auckland.jpg";
import React from "react";
import "./Sea.css";
import ImageSlider from "../ImageSlider";

const Sea = () => {
  const content = [img1, img2, img3];

  return (
    <>
      <div className="sea">
        <ImageSlider content={content} />
      </div>
    </>
  );
};

export default Sea;
