import img1 from "../images/CSL/44.jpg";
import img2 from "../images/CSL/49.jpg";
import img3 from "../images/CSL/51.jpg";
import img4 from "../images/CSL/52.jpg";
import img5 from "../images/CSL/48.jpg";
import React from "react";

import "./Road.css";
import ImageSlider from "../ImageSlider";

const Road = () => {
  const slides = [img1, img2, img3, img4, img5];

  return (
    <div className="road_redirect">
        <ImageSlider content={slides} />
    </div>
  );
};

export default Road;
