import img1 from "../images/CSL2/reefer-container-featured-e1591615532821.jpg";
import img2 from "../images/CSL2/shifting.jpg";
import img3 from "../images/CSL/57.jpg";
import img4 from "../images/CSL/project_cargo.jpeg";

import React from "react";
import ImageSlider from "../ImageSlider";

const Cargo = () => {
  const content = [img2, img4, img3, img1];

  return (
    <>
      <div className="Rail">
          <ImageSlider content={content} />
      </div>
    </>
  );
};

export default Cargo;
