import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Header from "./components/Header";
import Footer from "./components/Footer";
import Gallery from "./components/redirects/Gallery";
import About from "./components/redirects/About";
import Home from "./components/redirects/Home";
import Contact from "./components/redirects/Contact";
import Sea from "./components/redirects/Sea";
import Air from "./components/redirects/Air";
import Cargo from "./components/redirects/Cargo";
import Road from "./components/redirects/Road";
import Error from "./components/redirects/Error";

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          <Route path="/sea">
            <Sea />
          </Route>
          <Route path="/air">
            <Air />
          </Route>
          <Route path="/cargo">
            <Cargo />
          </Route>
          <Route path="/road">
            <Road />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route>
            <Error />
          </Route>
        </Switch>

        <Footer />
      </Router>
    </div>
  );
}

export default App;
